
.slider-container{
    
     height:70vh;
     width:100%;
     position:relative;
     margin: 0 auto;
     overflow:hidden;
}
.active{
    display:inline-block;
}
.inactive{
    display:none;
}
.slides{
    height:70vh;
    width:100%;
    position: relative;

}
.slide-image{
    width:100%;
    height:100%;
    position :absolute;
    object-fit: cover;
}

.slide-title, .slide-text{
    width:500px;
    height:400px;
    font-size:2.5rem;
    color:white;
    position:absolute;
    text-align:center;
    top:30%;
    left:30%;
    z-index:9;
   
}
.slide-text{
    top:50%;
}
.prev, .next{
    cursor:pointer;
    z-index:100;
    position:absolute;
    top:50%;
    width:auto;
    padding:1rem;
    margin-top:-3rem;
    font-size:30px;
    font-weight:bold;
    border-radius: 0 5px 5px 0;
}
.prev:hover,
.next:hover {
     color: white;
     background-color: rgba(0, 0, 0, 0.6);
     transition: all 0.5s ease-in;

}
.next{
    right:0;
    border-radius: 5px 0 0 5px;
}
.all-dots{
    width:100%;
    height:100%;
    position: absolute;
    display: flex;
    top:90%;
    justify-content:center;
    z-index:200;
}
.dot{
    cursor:pointer;
    height:1rem;
    width:1rem;
    margin:0 5px;
    background-color:rgba(0, 0, 0,0.3);
    border-radius: 50%;
    display:inline-block;
}
.active-dot, .dot:hover{
    background-color:rgba(255, 255, 255, 0.5);
}